@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz@9..40&family=Inter&family=Manrope:wght@200;300;400;500;600;700;800&family=Montserrat:wght@700&family=Taprom&display=swap');

$family-montserrat: "Montserrat", sans-serif;
$family-inter: "Inter", sans-serif;
$family-taprom: "Taprom", system-ui;
$family-manrope: "Manrope", sans-serif;
$family-dmsans: "DM sans", sans-serif;

$color-light-salmon: #FFA471;
$color-black: #000000;
$color-dim-gray: #6B6B6B;
$color-white: #FFFFFF;
$color-ghost-white: #F9F9F9;
$color-rich-black: #030303;


html, 
body, * { 
  margin: 0; padding: 0; 
  // margin: 0 auto;
}

body { /* ??? */
  // max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
}

.container {
  // min-width: 320px;
  // max-width: 1440px;
  // width: 100%;
  margin: 0 128px;
}
@media (max-width: 1440px) {
  .container {
    margin: 0 100px;
  }
}

@media (max-width: 1024px) {
  .container {
    margin: 0 70px;
  }
}
@media (max-width: 768px) {
  .container {
    margin: 0 10px;
  }
}

// .container {
//   // margin: 0 auto;
//   // padding: 0 150px;
//   width: 100%;
// }


@mixin onDesktop {
  @media (max-width: 1440px) {
    @content;
  }
}

@mixin onTablet {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin onMobile {
  @media (max-width: 768px) {
    @content;
  }
}